.summary-cards {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  flex-wrap: wrap;
}

.summary-cards .card {
  flex: 1;
  min-width: 160px;
  border-radius: 1rem;
  background: #ffffff;
  text-align: center;
  transition: transform 0.2s ease, background 0.3s ease, color 0.3s ease;
}
  
  body.dark-theme .summary-cards .card {
    background: #2b2b2b;
  }
  
  .summary-cards .card:hover {
    transform: translateY(-4px);
  }
  
  .card h3 {
    font-size: 1rem;
    margin-bottom: 0.5rem;
    color: #555;
  }
  
  body.dark-theme .card h3 {
    color: #ccc;
  }
  
  .card p {
    font-size: 1.3rem;
    font-weight: bold;
    color: #111;
  }
  
  body.dark-theme .card p {
    color: #eee;
  }
  
  .card.income {
    border-left: 6px solid #4caf50;
  }
  
  .card.expense {
    border-left: 6px solid #f44336;
  }
  
  .card.balance {
    border-left: 6px solid #007bff;
  }
  